import React from "react";
import "./css/createPost.css";
import ReactQuill from "react-quill";
import "react-quill/dist/quill.snow.css";
import { Navigate } from "react-router-dom";

const modules = {
  toolbar: [
    [{ header: [1, 2, false] }],
    ["bold", "italic", "underline", "strike", "blockquote"],
    [
      { list: "ordered" },
      { list: "bullet" },
      { indent: "-1" },
      { indent: "+1" }
    ],
    ["link", "image"],
    ["clean"]
  ]
};

const formats = [
  "header",
  "bold",
  "italic",
  "underline",
  "strike",
  "blockquote",
  "list",
  "bullet",
  "indent",
  "link",
  "image"
];

function CreatePost() {
  const [title, setTitle] = React.useState("");
  const [summary, setSummary] = React.useState("");
  const [content, setContent] = React.useState("");
  const [img, setImg] = React.useState("");
  const [redirect, setRedirect] = React.useState(false);
  const [myCategory, setMyCategory] = React.useState("Action");

  async function handleChange(e) {
    setMyCategory(e.target.value);
  }

  async function submit(e) {
    e.preventDefault();
    const data = new FormData();
    data.set("title", title);
    data.set("summary", summary);
    data.set("content", content);
    data.set("file", img[0]);
    data.set("category", myCategory);
    const response = await fetch("https://mystory-node.onrender.com/post", {
      method: "POST",
      body: data,
      credentials: "include"
    });

    await response.json();

    if (response.ok) {
      setRedirect(true);
    }
  }

  if (redirect) {
    return <Navigate to={"/"} />;
  }

  return (
    <form onSubmit={submit}>
      <input
        className='myInput'
        type="title"
        onChange={(e) => setTitle(e.target.value)}
        name="title"
        placeholder={"Title"}
        value={title}
        required
      />
      <input
        className='myInput'
        type="summary"
        onChange={(e) => setSummary(e.target.value)}
        placeholder={"Summary"}
        value={summary}
        required
      />

      <div className="categorySelect">
        <label htmlFor="category">Please Select a Category</label>
        <select
          id="category"
          value={myCategory}
          onChange={handleChange}
          required
        >
          <option value="Action">Action</option>
          <option value="Adventure">Adventure</option>
          <option value="Comedy">Comedy</option>
          <option value="Crime">Crime</option>
          <option value="Drama">Drama</option>
          <option value="Fable">Fable</option>
          <option value="Fantasy">Fantasy</option>
          <option value="Fan Fics">Fan Fics</option>
          <option value="Fiction">Fiction</option>
          <option value="Historical">Historical</option>
          <option value="Horror">Horror</option>
          <option value="Legend">Legend</option>
          <option value="Mystery">Mystery</option>
          <option value="Myth">Myth</option>
          <option value="Non-Fiction">Non-Fiction</option>
          <option value="Paranormal">Paranormal</option>
          <option value="Romance">Romance</option>
          <option value="Realistic">Realistic</option>
          <option value="Short Story">Short Story</option>
          <option value="Thriller">Thriller</option>
          <option value="True Story">True Story</option>
          <option value="Wholesome">Wholesome</option>
        </select>
      </div>
      <label htmlFor="image">Upload a Title Picture</label>
      <input
        className='myInput'
        id="image"
        type="file"
        accept="image/png, image/jpeg"
        onChange={(e) => setImg(e.target.files)}
        required
      />
      <ReactQuill
        name="content"
        onChange={(e) => setContent(e)}
        value={content}
        modules={modules}
        formats={formats}
        required
      />
      <button type="submit" className="create-post">
        Create Post
      </button>
    </form>
  );
}

export default CreatePost;
