import React from "react";

import { Navigate } from "react-router-dom";

import Select from "./Select";

import "./css/stack.css";

import {
  Stack,
  Button,
  TextField,
  FormControl,
  FormLabel,
  FormControlLabel,
  RadioGroup,
  Radio
} from "@mui/material";

function Register() {
  // Declaring State
  const [state, setState] = React.useState({
    fName: "",
    lName: "",
    gender: "Male",
    age: "1",
    email: "",
    username: "",
    password: "",
    repeatPassword: "",
    showPassword: false
  });

  const [img, setImg] = React.useState("");
  const [redirect, setRedirect] = React.useState(false);
  //End State Declaration

  // ---------------------------------------------------------------------------

  // Handling Change
  function handleChange(e) {
    const name = e.target.name;
    const value = e.target.value;

    setState((prev) => {
      return {
        ...prev,
        [name]: value
      };
    });
  }

  function handleRadio(e: React.ChangeEvent<HTMLInputElement>) {
    setState((prev) => {
      return {
        ...prev,

        gender: e.target.value
      };
    });
  }

  function handleClick() {
    return setState((prev) => ({
      ...prev,

      showPassword: !state.showPassword
    }));
  }

  // -----------------------------------------------------------------------------

  // Submit Function
  async function submit(e) {
    e.preventDefault();

    try {
      if (state.password !== state.repeatPassword) {
        return window.alert("Passwords do not match");
      }

      if (state.password.length === 0 || state.repeatPassword.length === 0) {
        return window.alert("Password Field is empty");
      }

      const myData = new FormData();

      myData.set("fName", state.fName);
      myData.set("lName", state.lName);
      myData.set("gender", state.gender);
      myData.set("age", state.age);
      myData.set("email", state.email);
      myData.set("username", state.username);
      myData.set("password", state.password);

      if (img !== "") {
        myData.set("pfp", img[0]);
      } else {
        setImg(null);
        myData.set("pfp", img);
      }

      const res = fetch("https://mystory-node.onrender.com/register", {
        method: "POST",
        body: myData
      });

      if (!res.ok) {
        return console.log(res.json);
      }
      window.alert("Registeration Successfull");

      setRedirect(true);
    } catch (error) {
      return console.log(error);
    }
  } // Submit Function Ends

  // -------------------------------------------------------------------------

  if (redirect) {
    return <Navigate to={"/"} />;
  }

  return (
    <Stack className="stack" component="form" onSubmit={submit} spacing={5}>
      <h2>First Name</h2>
      <TextField
        InputProps={{ className: "text-field" }}
        name="fName"
        autoComplete="on"
        varient="filled"
        value={state.fName}
        onChange={handleChange}
        required
      />
      <h2>Last Name</h2>
      <TextField
        InputProps={{ className: "text-field" }}
        name="lName"
        autoComplete="on"
        varient="filled"
        value={state.lName}
        onChange={handleChange}
        required
      />

      <FormControl>
        <FormLabel id="gender">
          <h2>Gender</h2>
        </FormLabel>
        <RadioGroup
          aria-labelledby="gender"
          defaultValue="Male"
          value={state.gender}
          name="gender"
          row={true}
          onChange={handleRadio}
        >
          <FormControlLabel value="Male" control={<Radio />} label="Male" />
          <FormControlLabel value="Female" control={<Radio />} label="Female" />
          <FormControlLabel value="Other" control={<Radio />} label="Other" />
        </RadioGroup>
      </FormControl>
      <Select setState={setState} value={state.age} />

      <h2>Email</h2>
      <TextField
        InputProps={{ className: "text-field" }}
        name="email"
        autoComplete="on"
        varient="filled"
        value={state.email}
        onChange={handleChange}
        required
      />

      <h2>Username</h2>
      <TextField
        InputProps={{ className: "text-field" }}
        name="username"
        autoComplete="on"
        varient="filled"
        value={state.username}
        onChange={handleChange}
        required
      />

      <h2>Password</h2>
      <div className="passwordContainer">
        <TextField
          error={state.password !== state.repeatPassword}
          InputProps={{ className: "text-field" }}
          type={state.showPassword ? "text" : "password"}
          name="password"
          autoComplete="on"
          varient="filled"
          value={state.password}
          onChange={handleChange}
          required
        />{" "}
        {state.showPassword ? (
          <div
            type="button"
            onClick={handleClick}
            className="fa-solid fa-eye-slash eyeButton"
          ></div>
        ) : (
          <div
            type="button"
            onClick={handleClick}
            className="fa-solid fa-eye eyeButton"
          ></div>
        )}
      </div>
      <h2>Re-type Password</h2>
      <div className="passwordContainer">
        <TextField
          helperText={
            state.password !== state.repeatPassword && "Passwords do not match"
          }
          error={state.password !== state.repeatPassword}
          InputProps={{ className: "text-field" }}
          type={state.showPassword ? "text" : "password"}
          name="repeatPassword"
          autoComplete="on"
          varient="filled"
          value={state.repeatPassword}
          onChange={handleChange}
          required
        />
        {state.showPassword ? (
          <div
            type="button"
            onClick={handleClick}
            className="fa-solid fa-eye-slash eyeButton"
          ></div>
        ) : (
          <div
            type="button"
            onClick={handleClick}
            className="fa-solid fa-eye eyeButton"
          ></div>
        )}
      </div>

      <h5>Profile Picture</h5>
      <input
        id="image"
        name="image"
        type="file"
        accept="image/png, image/jpeg"
        onChange={(e) => setImg(e.target.files)}
      />

      <Button size="large" type="submit" variant="contained" color="secondary">
        Submit
      </Button>
    </Stack>
  );
}

export default Register;
