import React from "react";
import "./css/Post.css";
import { format } from "date-fns";
import { Link } from "react-router-dom";

function Post({ _id, title, summary, cover, content, createdAt, author }) {
  function clip(data) {
    return data.substring(0, 250);
  }
  return (
    <div className="post">
      <div className="image">
        <Link to={`/post/${_id}`}>
          <img src={"https://mystory-node.onrender.com/" + cover} alt="" />
        </Link>
      </div>

      <div className="text">
        <Link to={`/post/${_id}`}>
          <h2>{title}</h2>
        </Link>
        <p className="info">
          <a href="/" className="author">
            {author.username}
          </a>
          <time> {format(new Date(createdAt), "MMM d, yyyy hh:mm aaa")}</time>
        </p>
        <p className="summary">{clip(summary)}</p>
      </div>
    </div>
  );
}

export default Post;
