import React from "react";
import myCategories from "./myCategories";
import "@coreui/coreui/dist/css/coreui.min.css";
import Card from "./Card";
import "./css/cardImage.css";
import {
  CCard,
  CCardBody,
  CCardText,
  CCardTitle,
  CButton,
  CCardImage,
  CRow,
  CCol,
  CContainer
} from "@coreui/react";

function CategoryPage() {
  return (
    <CContainer className="myContainer">
      <CRow
        className="format"
        xs={{ cols: 1, gutter: 4 }}
        sm={{ cols: 2 }}
        md={{ cols: "auto" }}
      >
        <Card />
      </CRow>
    </CContainer>
  );
}

export default CategoryPage;
