import React from "react";

import "./css/stack.css";

import { Stack, Button, TextField } from "@mui/material";

import { Navigate , Link } from "react-router-dom";

import { UserContext } from "../UserContext";

function Login() {
  const [state, setState] = React.useState({
    cred: "",
    password: "",
    showPassword: false
  });

  const { setUserInfo } = React.useContext(UserContext);

  const [redirect, setRedirect] = React.useState(false);

  function handleChange(e) {
    const name = e.target.name;
    const value = e.target.value;

    setState((prev) => {
      return {
        ...prev,
        [name]: value
      };
    });
  }


  function handleClick(){


      return setState(prev => 

        ({

        ...prev,

        showPassword: !state.showPassword

      }));


  }

  async function submit(e) {
    e.preventDefault();

    try {
      const res = await fetch("https://mystory-node.onrender.com/login", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          username: state.cred,
          password: state.password
        }),
        credentials: "include"
      });

      if (res.ok) {
        res.json().then((userInfo) => {
          setUserInfo(userInfo);
          setRedirect(true);
        });
      } else {
        window.alert("Login Failed!");
      }
    } catch (error) {
      return console.log("fetch error");
    }
  }

  if (redirect) {
    return <Navigate to={"/"} />;
  }

  return (
    <Stack className="stack" component="form" onSubmit={submit} spacing={5}>
      <h2>Email or Username</h2>
      <TextField
        InputProps={{ className: "text-field" }}
        name="cred"
        autoComplete="on"
        varient="filled"
        value={state.cred}
        onChange={handleChange}
        required
      />

      <h2>Password</h2>
      <div className="passwordContainer">
      <TextField
        error={state.password !== state.repeatPassword}
        InputProps={{ className: "text-field" }}
        type={state.showPassword ? "text" : "password"}
        name="password"
        autoComplete="on"
        varient="filled"
        value={state.password}
        onChange={handleChange}
        required
      /> {state.showPassword  ? <div type='button' onClick={handleClick} className="fa-solid fa-eye-slash eyeButton"></div> : <div type='button' onClick={handleClick} className="fa-solid fa-eye eyeButton"></div>}
      </div>
      <Button type="submit" size="large" variant="contained" color="secondary">
        Submit
      </Button>
      <Link to='/forgotPassword'>Forgot Password</Link>
    </Stack>
  );
}

export default Login;
