import React from 'react';
import "./css/password-reset.css";
import { Link, useParams, Navigate } from "react-router-dom";

export default function ChangePassword(props){

  const [oldPass, setOldPass] = React.useState("");    
  const [pass, setPass] = React.useState("");

  const [rePass, setRePass] = React.useState("");

  const [linkStatus, setLinkStatus] = React.useState(false);

  const [button1, setButton1] = React.useState(false);
  const [button2, setButton2] = React.useState(false);
  const [button3, setButton3] = React.useState(false);

  const [message, setMessage] = React.useState(false);

  const [myMessage, setMyMessage] = React.useState(false);
  const [error, setError] = React.useState('');
  const [checkError, setCheckError] = React.useState(false);

  const [redirect, setRedirect] = React.useState(false);


  React.useEffect(() => {
    if (pass.length <= 8) {
      setMyMessage(true);
    } else {
      setMyMessage(false);
    }
  }, [pass]);


function handleSubmit(e){

e.preventDefault();

if (pass === rePass) {
      setMessage(false);
      if (pass.length >= 8) {
        

        fetch(`https://mystory-node.onrender.com/reset-password/`, {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          credentials: "include",
          body: JSON.stringify({
            old: oldPass,
            newPassword: pass,
            id:props.id
          })
        }).then(response => {

		response.json().then(info => {

			if(info.statusCode === 1){

				setRedirect(true);

			}else if(info.statusCode === 15){

				setCheckError(true);

				setError('Old Password is Incorrect! Try Again');

			}


		})

	})




      } else {
        setMyMessage(true);
      }
    } else {
      setMessage(true);
    }
  }

if (redirect) {
    return <Navigate to={"/"} />;
  }



return(

<form onSubmit={handleSubmit}>
	
	

<div className="reset-password">
           <h2>Change Your Password</h2>
            <br />
            <br />


<div className="fields">
              <span className="block"><strong>Old Password: </strong></span>
              <div>
                <input
                  onChange={(e) => {
                    setOldPass(e.target.value);
                  }}
                  className="block"
                  type={button3 ? "text" : "password"}
                  value={oldPass}
                  placeholder="Enter The Old Password"
                  name="oldPassword"
                  required
                />
                {button3 ? (
                  <div
                    type="button"
                    onClick={() => {
                      setButton3(!button3);
                    }}
                    name="oldRe-password"
                    className="fa-solid fa-eye-slash eyeButton"
                  ></div>
                ) : (
                  <div
                    type="button"
                    onClick={() => {
                      setButton3(!button3);
                    }}
                    name="oldRe-password"
                    className="fa-solid fa-eye eyeButton"
                  ></div>
                )}
              </div>
              <br />

            </div>












            <div className="fields">
              <span className="block"><strong>New Password: </strong></span>
              <div>
                <input
                  onChange={(e) => {
                    setPass(e.target.value);
                  }}
                  className="block"
                  type={button1 ? "text" : "password"}
                  value={pass}
                  placeholder="Enter The New Password"
                  name="password"
                  required
                />
                {button1 ? (
                  <div
                    type="button"
                    onClick={() => {
                      setButton1(!button1);
                    }}
                    name="re-password"
                    className="fa-solid fa-eye-slash eyeButton"
                  ></div>
                ) : (
                  <div
                    type="button"
                    onClick={() => {
                      setButton1(!button1);
                    }}
                    name="re-password"
                    className="fa-solid fa-eye eyeButton"
                  ></div>
                )}
              </div>
              <br />
   
            </div>

            <div className="fields">
              <span className="block"><strong>Retype Password:</strong> </span>
              <div>
                <input
                  onChange={(e) => {
                    setRePass(e.target.value);
                  }}
                  className="block"
                  type={button2 ? "text" : "password"}
                  value={rePass}
                  placeholder="Retype The Password"
                  name="password"
                  required
                />
                {button2 ? (
                  <div
                    type="button"
                    onClick={() => {
                      setButton2(!button2);
                    }}
                    name="re-password"
                    className="fa-solid fa-eye-slash eyeButton"
                  ></div>
                ) : (
                  <div
                    type="button"
                    onClick={() => {
                      setButton2(!button2);
                    }}
                    name="re-password"
                    className="fa-solid fa-eye eyeButton"
                  ></div>
                )}
                {pass !== rePass && (
                  <p className="color">The Passwords Do Not Match!</p>
                )}

                {pass === "" && (
                  <p className="color">Please Enter the Passwords</p>
                )}

                {message && (
                  <p className="color">The Passwords Do Not Match!</p>
                )}
                {myMessage && (
                  <p className="color">
                    The Password needs to be minimum 8 Characters!
                  </p>
                )}
              </div>
              <br />
        		
        	{checkError && <p>{error}</p>}

            </div>

            <button className="btn-form">Submit</button>


</div>


</form>

	);

} 