import React from "react";
import "./css/panel.css";
import { UserContext } from "../UserContext";
import Post from "./Post";
import "./css/dashboard.css";

function SavedPosts() {
  const { userInfo, setUserInfo } = React.useContext(UserContext);
  const [mySavedPosts, setMySavedPosts] = React.useState([]);

  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/getUserPosts", {
      credentials: "include"
    }).then((response) => {
      response.json().then((info) => {
        setMySavedPosts(info);
      });
    });
  }, []);

  return (
    <div className="container-panel">
      <div className="saved-post-container">
        <h3>Saved Posts</h3>
      </div>
      <div className="post-contain">
        <div className="singlePost">
          {mySavedPosts.length > 0 &&
            mySavedPosts.map((post, index) => <Post {...post} key={index} />)}
        </div>
      </div>
    </div>
  );
}

export default SavedPosts;
