import React from "react";
import myCategories from "./myCategories";
import "./css/cardImage.css";
import "@coreui/coreui/dist/css/coreui.min.css";
import {
  CCard,
  CCardBody,
  CCardText,
  CCardTitle,
  CButton,
  CCardImage,
  CRow,
  CCol
} from "@coreui/react";

function Card() {
  return myCategories.map((category, index) => (
    <CCol xs className="cardCenter" key={index}>
      <CCard className="h-100" style={{ width: "13rem" }}>
        <CCardImage
          className="cardImage"
          orientation="top"
          src={category.img}
        />
        <CCardBody>
          <CCardTitle>{category.name}</CCardTitle>
          <CCardText>{category.desc}</CCardText>
          <CButton href={category.buttonLink}>{category.buttonText}</CButton>
        </CCardBody>
      </CCard>
    </CCol>
  ));
}

export default Card;
