import React from "react";
import Post from "./Post";
import "./css/index.css";
import { UserContext } from "../UserContext";
import { Link } from "react-router-dom";
import CategorySelector from "./CategorySelector";
import myCategories from "./myCategories";

function Index() {
  const [posts, setPosts] = React.useState([]);

  const { userInfo } = React.useContext(UserContext);

  const [state, setState] = React.useState(false);

  const [postID, setPostID] = React.useState(0);
  const [postName, setPostName] = React.useState("");

  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/myposts", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },

      body: JSON.stringify({
        categoryID: postID,
        name: postName
      })
    }).then((response) => {
      response.json().then((post) => setPosts(post));
    });
  }, [postID]);

  React.useEffect(() => {
    if (userInfo) {
      setState(true);
    } else {
      setState(false);
    }
  }, [userInfo]);

  function click(id, name) {
    setPostID(id);
    setPostName(name);
  }

  return (
    <>
      <div className="title">
        <div>
          <h1>Hi!! Welcome to Story App!</h1>
          <p>Create your stories and read other stories</p>

          {state ? (
            <div>
              <Link to="/create">
                <button name="post">
                  Create Post{" "}
                  <i
                    style={{ marginLeft: "10px" }}
                    className="fa-solid fa-pen fa-beat-fade"
                  ></i>
                </button>
              </Link>
            </div>
          ) : (
            <div>
              <Link to="/login">
                <button name="login">Login</button>
              </Link>
              <Link to="/register">
                <button name="register">Register</button>
              </Link>
            </div>
          )}
        </div>

        <img
          className="home-img"
          src="https://img.freepik.com/premium-vector/teacher-read-story-student-vector-illustration_97632-350.jpg"
          alt="img"
        />
      </div>

      <div className="PostSection">
        <h1 className="heading">List of Posts</h1>

        {myCategories.map((category, index) => (
          <CategorySelector
            onClick={click}
            id={index}
            key={index}
            name={category.name}
          />
        ))}

        {posts.length > 0 &&
          posts.map((post, index) => <Post {...post} key={index} />)}
      </div>
    </>
  );
}

export default Index;
