import "./css/comment.css";
import React from "react";
import { UserContext } from "../UserContext";
import { useParams } from "react-router-dom";

function Comments(props) {
  const { userInfo } = React.useContext(UserContext);

  const id = props.postID;

  const [comment, setComment] = React.useState("");
  const [allComments, setAllComments] = React.useState([]);
  const [commentState, setCommentState] = React.useState(false);

  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/getComments", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        name: userInfo.username,
        comments: comment,
        pfp: "https://mystory-node.onrender.com/" + userInfo.pfp,
        id: id
      }),
      credentials: "include"
    }).then((response) => {
      response.json().then((info) => {
        setAllComments(info.comments);
      });
    });
  },[commentState]);

  function handleChange(e) {
    const value = e.target.value;

    setComment(value);
  }

  function onButtonClick() {
    var res;

    if (userInfo) {
      res = fetch("https://mystory-node.onrender.com/postComment", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          name: userInfo.username,
          comments: comment,
          pfp: "https://mystory-node.onrender.com/" + userInfo.pfp,
          id: id
        }),
        credentials: "include"
      }).then((response) => {
        response.json().then((info) => {
          if (!res.ok) {
        
          }
        });
      });

      setComment("");
      setTimeout(()=>{setCommentState(!commentState)},500);

    } else {
      res = fetch("https://mystory-node.onrender.com/postComment", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify({
          name: "Anonymous",
          comments: comment,
          pfp: "https://mystory-node.onrender.com/uploads/basic.png",
          id: id
        }),
        credentials: "include"
      }).then((response) => {
        response.json().then((info) => {
          if (!res.ok) {
            
          }
        });
      });

      setComment("");
      setTimeout(()=>{setCommentState(!commentState)},500);
    }
  }

  return (
    <div className="comment-box">
      <h1 className="title">Comments</h1>
      <input
        name="commentInput"
        className="input-comment"
        type="text"
        placeholder="Write your comment here"
        value={comment}
        onChange={handleChange}
      />
      <button name="commentButton" onClick={onButtonClick}>
        Comment
      </button>

      {allComments.map((myComment, index) => {
        return (
          <div className="comment-post" key={index}>
            <div className="comment">
              <div className="profile">
                <img className="pfp" src={myComment.pfp} />
                <div>{myComment.name}</div>
              </div>
              <div className="userComment">
                <p>{myComment.comment}</p>
                <hr />
              </div>
            </div>
          </div>
        );
      })}
    </div>
  );
}

export default Comments;
