import React from "react";

function Select(props) {
  function isChecked(e) {
    const drop = document.getElementById("age");

    props.setState((prev) => {
      return {
        ...prev,

        age: drop.options[drop.selectedIndex].text
      };
    });
  }

  function options() {
    const indents = [];

    for (var i = 1; i < 101; i++) {
      indents.push(
        <option key={i} name="age" value={i}>
          {i}
        </option>
      );
    }

    return indents;
  }

  return (
    <div>
      <label htmlFor="age">
        <h5>Age</h5>
      </label>
      <select id="age" name="age" onChange={isChecked}>
        {options()}
      </select>
    </div>
  );
}

export default Select;
