import React from "react";
import "./css/editScreen.css";

function EditData(props) {
  const [name, setName] = React.useState(false);
  const [age, setAge] = React.useState(false);
  const [email, setEmail] = React.useState(false);
  const [pfp, setPfp] = React.useState(false);
  const [myName, setMyName] = React.useState("");
  const [fullName, setFullName] = React.useState({
    fName: "",
    lName: ""
  });
  const [newAge, setNewAge] = React.useState(0);
  const [newEmail, setNewEmail] = React.useState("");
  const [newFile, setNewFile] = React.useState("");

  function handleName(e) {
    const name = e.target.name;
    const value = e.target.value;


    if (name) {
      setFullName((prev) => {
        return { ...prev, [name]: value };
      });
    } else if (age) {
      setNewAge(value);
    } else if (email) {
      setNewEmail(value);
    } else if(pfp){

    	setNewFile(e.target.files);

    }

  }

  function selectInput() {
    if (props.name === "name") {
      setName(true);
      setAge(false);
      setEmail(false);
      setPfp(false);
    } else if (props.name === "age") {
      setName(false);
      setAge(true);
      setEmail(false);
      setPfp(false);
    } else if (props.name === "email") {
      setName(false);
      setAge(false);
      setEmail(true);
      setPfp(false);
    } else if (props.name === "pfp") {
      setName(false);
      setAge(false);
      setEmail(false);
      setPfp(true);
    }
  }

  React.useEffect(() => {
    selectInput();
  }, []);

  function handleSubmit(e) {
    e.preventDefault();
    const attribute = e.target.getAttribute("name");

    const data = new FormData();
    data.set('id', props.userID);

    if (attribute === "name") {
      	
    	data.set('fName' , fullName.fName);
    	data.set('lName', fullName.lName);

      fetch('https://mystory-node.onrender.com/editUser',{

      	method: 'POST',
      	credentials:'include',
      	body: data

      }).then(response => {

      	response.json().then(info => {


      	});

      });



    } else if (attribute === "age") {
      
      data.set('age', newAge);

      fetch('https://mystory-node.onrender.com/editUser',{

      	method: 'POST',
      	credentials:'include',
      	body: data

      }).then(response => {

      	response.json().then(info => {


      	});


      });

    } else if (attribute === "email") {
      
    	data.set('email', newEmail);


      fetch('https://mystory-node.onrender.com/editUser',{

      	method: 'POST',
      	credentials:'include',
      	body: data

      }).then(response => {

      	response.json().then(info => {


      	});


      });


    } else if (attribute === "pfp") {
	
	data.set('pfp', newFile[0]);

      fetch('https://mystory-node.onrender.com/editUser',{

      	method: 'POST',
      	credentials:'include',
      	body: data

      }).then(response => {

      	response.json().then(info => {


      	});


      });

    }

    props.onClick();

  }

  return (
    <div className="editScreen">
      <div className="editFields">
        <div className="closeButton" type="button" onClick={props.onClick}>
          <i className="fa-solid fa-xmark fa-xl"></i>
        </div>
        <div className="data">
          <h3>Edit Data</h3>
          {name && (
            <div>
              <div className="Names">
                <div className="contain">
                  <div>
                    <div className="fieldStyle">First Name:</div>
                    <input
                      type="text"
                      name="fName"
                      onChange={handleName}
                      value={fullName.fName}
                    />
                  </div>
                  <div>
                    <div className="fieldStyle">Last Name:</div>
                    <input
                      type="text"
                      name="lName"
                      onChange={handleName}
                      value={fullName.lName}
                    />
                  </div>
                  <button
                    onClick={handleSubmit}
                    name="name"
                    className="editSubmit"
                  >
                    Submit
                  </button>
                </div>
              </div>
            </div>
          )}
          {age && (
            <div>
              <div className="contain">
                <div className="fieldStyle">Age: </div>
                <input type="text" value={newAge} onChange={handleName} />
              </div>
              <button onClick={handleSubmit} name="age" className="editSubmit">
                Submit
              </button>
            </div>
          )}
          {email && (
            <div>
              <form>
                <div className="contain">
                  <div className="fieldStyle">Email: </div>
                  <input
                    pattern="[a-zA-Z0-9.!#$%&’*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*"
                    type="email"
                    value={newEmail}
                    onChange={handleName}
                  />
                </div>
                <button
                  onClick={handleSubmit}
                  name="email"
                  className="editSubmit"
                >
                  Submit
                </button>
              </form>
            </div>
          )}
          {pfp && (
            <div>
              <form>
                <div className="contain">
                  <div className="fieldStyle">Profile Picture: </div>
                  <input type="file" onChange={handleName} />
                </div>
                <button
                  onClick={handleSubmit}
                  name="pfp"
                  className="editSubmit"
                >
                  Submit
                </button>
              </form>
            </div>
          )}
        </div>
      </div>
    </div>
  );
}

export default EditData;
