import React from "react";
import { useParams } from "react-router-dom";
import "./css/postpage.css";
import { format } from "date-fns";
import { UserContext } from "../UserContext";
import { Link } from "react-router-dom";
import Comments from "./Comments";
import Star from "./Star";

function PostPage() {
  const [postInfo, setPostInfo] = React.useState(null);
  const { userInfo } = React.useContext(UserContext);

  const { id } = useParams();
  const [save, setSave] = React.useState(false);
  React.useEffect(() => {
    fetch(`https://mystory-node.onrender.com/post/${id}`).then((response) => {
      response.json().then((info) => {
        setPostInfo(info);
      });
    });
  }, []);

  function handleClick() {
    fetch("https://mystory-node.onrender.com/savePost", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },

      body: JSON.stringify({
        postID: id,
        saveState: save,
        username: userInfo.username,
        userID: userInfo.id
      })
    }).then((response) => {
      response.json().then((info) => {
        setSave(info.State);
      });
    });
  }

  if (!postInfo) {
    return "No Post Found";
  }

  return (
    <div>
      <div className="page">
        <div className="post-page">
          <h1>{postInfo.title}</h1>
          <div className="time">
            <time>
              {format(new Date(postInfo.createdAt), "MMM d, yyyy hh:mm aaa")}
            </time>
            <p>By</p>
            <p>{postInfo.author.username}</p>
            {String(userInfo.id) === String(postInfo.author._id) && (
              <div className="userDate">
                <Link
                  type="button"
                  className="button1"
                  to={`/edit/${postInfo._id}`}
                >
                  <i className="fa-regular fa-pen-to-square"></i> Edit This
                </Link>

                {userInfo && (
                  <Star
                    setState={setSave}
                    state={save}
                    Clicked={handleClick}
                    postID={id}
                    userID={userInfo.id}
                    username={userInfo.username}
                  />
                )}
              </div>
            )}
          </div>
          <div className="image">
            <img src={`https://mystory-node.onrender.com/${postInfo.cover}`} />
          </div>

          <div
            className="content"
            dangerouslySetInnerHTML={{ __html: postInfo.content }}
          />
        </div>
      </div>

      <Comments postID={id} />
    </div>
  );
}

export default PostPage;
