import React from "react";

function Star(props) {
  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/savePostStatus", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({
        postID: props.postID,
        username: props.username,
        userID: props.userID
      })
    }).then((response) => {
      response.json().then((info) => {
        props.setState(info);
      });
    });
  }, []);

  return (
    <div>
      {props.state ? (
        <div type="button" onClick={props.Clicked} className="saveBtn">
          <i
            className="fa-solid fa-star fa-2xl"
            style={{ color: "#fff70a" }}
          ></i>
        </div>
      ) : (
        <div type="button" onClick={props.Clicked} className="saveBtn">
          <i
            className="fa-regular fa-star fa-2xl"
            style={{ color: "#fff70a" }}
          ></i>
        </div>
      )}
    </div>
  );
}

export default Star;
