import { createTheme, responsiveFontSizes } from '@mui/material/styles'

let theme = createTheme({
  palette: {
    primary: {
      main: '#36B53D',
      light: '#49F251',
      dark: '#007306',
      contrastText: '#fff',
    },
    secondary: {
      main: '#05A64B',
      light: '#50F296',
      dark: '#037534',
      contrastText: '#fff',
    },
  },
  components: {
    MuiPaper: {
      styleOverrides: {
        root: {
          backgroundColor: '#e1e2e1',
          // backgroundColor: '#F5F5F6',
        },
      },
    },
  },
})

theme = responsiveFontSizes(theme)

export default theme