import React from "react";
import { useLocation } from "react-router-dom";
import Post from "./Post";

function QSearch() {
  const [posts, setPosts] = React.useState([]);

  function useQuery() {
    return new URLSearchParams(useLocation().search);
  }

  const myQuery = useQuery().get("q");

  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/search", {
      method: "POST",
      headers: {
        "Content-Type": "application/json"
      },
      body: JSON.stringify({
        query: myQuery
      })
    }).then((response) => {
      response.json().then((info) => {
        setPosts(info);
      });
    });
  }, [myQuery]);

  return (
    <div>
      <div className="post-container">
        <h3>Search Results for {myQuery}</h3>

        {posts.length < 1 ? (
          <p>No Posts</p>
        ) : (
          posts.map((post, index) => <Post {...post} key={index} />)
        )}
      </div>
    </div>
  );
}

export default QSearch;
