import React from "react";
import "./css/panel.css";
import { useParams, Link } from "react-router-dom";
import { format } from "date-fns";
import EditData from "./EditData";

function Panel(props) {
  const [userData, setUserData] = React.useState({});
  const [checkEdit, setCheckEdit] = React.useState({ field: "" });
  const [click, setClick] = React.useState(false);
  const [close, setClosed] = React.useState(false);

  React.useEffect(() => {
    fetch("https://mystory-node.onrender.com/getUser", {
      credentials: "include"
    }).then((response) => {
      response.json().then((info) => {
        setUserData(info);
      });
    });
  }, [click]);

  function handleClick(e) {
    const field = e.target.getAttribute("name");

    setCheckEdit({ field: field });
    setClick(true);
  }

  function handleClose() {
    setTimeout(() => {
      setClick(false);
    }, 500);
  }

  return (
    <div className="container-panel">
      <div className="image-container">
        <img
          src={`https://mystory-node.onrender.com/${userData.pfp}`}
          alt="picture"
          className="myPfp"
        />
        <i
          type="button"
          onClick={handleClick}
          className="fa-solid fa-pen-to-square pfpBtn fa-lg"
          name="pfp"
          style={{ color: "#999" }}
        ></i>
      </div>
      <div className="details">
        <div className="entry">
          <div>
            <span className="fields">Name:</span>
            <span> {userData.fName + " " + userData.lName}</span>
          </div>{" "}
          <i
            type="button"
            onClick={handleClick}
            className="fa-solid fa-pen-to-square"
            name="name"
            style={{ color: "#303030" }}
          ></i>
        </div>
        <div className="entry">
          <div>
            <span className="fields">ID:</span>
            <span> {userData._id}</span>
          </div>
          <div></div>
        </div>
        <div className="entry">
          <div>
            <span className="fields">Age:</span>
            <span> {userData.age}</span>
          </div>{" "}
          <i
            type="button"
            onClick={handleClick}
            className="fa-solid fa-pen-to-square"
            name="age"
            style={{ color: "#303030" }}
          ></i>
        </div>
        <div className="entry">
          <div>
            <span className="fields">Email:</span>
            <span> {userData.email}</span>
          </div>{" "}
          <i
            type="button"
            onClick={handleClick}
            className="fa-solid fa-pen-to-square"
            name="email"
            style={{ color: "#303030" }}
          ></i>
        </div>
        <div className="entry">
          <div>
            <span className="fields">Username:</span>
            <span> {userData.username}</span>
          </div>{" "}
          <div></div>
        </div>
        
        <div className="entry">
          <div>
            <span className="fields">Password:</span>
            <span>***********</span>
          </div>{" "}
          <Link to={'/forgotPassword'}>
          <i
            type="button"
            className="fa-solid fa-pen-to-square"
            name="age"
            style={{ color: "#303030" }}
          ></i>
          </Link>
          </div>



        <div className="entry">
          <div>
            <span className="fields">Account Created on:</span>
            <span>
              {" "}
              {userData.createdOn
                ? format(new Date(userData.createdOn), "MMM d, yyyy")
                : userData.createdOn}
            </span>
          </div>
          <div></div>
        </div>

        {click && (
          <EditData
            name={checkEdit.field}
            onClick={handleClose}
            userID={userData._id}
          />
        )}
      </div>
    </div>
  );
}

export default Panel;
