import React from 'react';
import './css/password-reset.css';

export default function ResetPassword(){


const [email, setEmail] = React.useState(''); 
const [error, setError] = React.useState({});
function handleChange(e){

	const value = e.target.value;
	setEmail(value);

}

function handleClick(e){
e.preventDefault();

	fetch('https://mystory-node.onrender.com/resetPassword',{

		method:'POST', 
		headers:{

			'Content-Type': 'application/json'
		},
		credentials: 'include',
		body:JSON.stringify({

			email: email

		}),


	}).then(response => {

		response.json().then(info => {

			setError(info);

		})

	})






}




return(

<div className='reset-password'>
	
	<h2>Password Reset Page</h2>
	<br />
	<form onSubmit={handleClick}>
	<p>Enter your Email:</p>
	<input className='email-input' pattern="(?!(^[.-].*|[^@]*[.-]@|.*\.{2,}.*)|^.{254}.)([a-zA-Z0-9!#$%&'*+\/=?^_`{|}~.-]+@)(?!-.*|.*-\.)([a-zA-Z0-9-]{1,63}\.)+[a-zA-Z]{2,15}" type="email" onChange={handleChange} value={email} required />
	{error.statusCode !== 1 ? <p style={{color: 'red'}}>{error.msg}</p> : <p style={{color: 'green'}}>An Email Has Been Sent</p>}
	<br />
	<br />
	<button className='btn-form'>Submit</button>
</form>
</div>


	);

} 