import React from "react";
import { Link } from "react-router-dom";
import "./css/Header.css";
import { useEffect } from "react";
import { UserContext } from "../UserContext";
import SearchBar from "./SearchBar";
import "./css/search.css";

function Header() {
  const { userInfo, setUserInfo } = React.useContext(UserContext);
  const [mySearch, setMySearch] = React.useState(false);

  const [click, setClick] = React.useState(false);
  function handleClick() {
    setClick(!click);
  }

  useEffect(() => {
    fetch("https://mystory-node.onrender.com/profile", {
      credentials: "include"
    }).then((response) => {
      response.json().then((json) => {
        setUserInfo(json);
      });
    });
  }, []);

  function logout() {
    fetch("https://mystory-node.onrender.com/logout", {
      credentials: "include",
      method: "POST"
    });

    setUserInfo(null);
  }

  function handleSearchClick(e) {
    setMySearch(!mySearch);
  }

  const username = userInfo?.username;

  return (
    <header>
      <Link to="/" className="logo">
        MyStory
      </Link>

      <nav>
        {username && (
          <>
            <ul className={click ? "normal active" : "normal"}>
              <li className="listItem">
                <button
                  className="searchButton myBtn"
                  onClick={handleSearchClick}
                >
                  <i className="fa-solid fa-magnifying-glass"></i>Search
                </button>
              </li>
              {mySearch && <SearchBar onClick={handleSearchClick} />}

              <Link to="/categories">
                <li className="listItem">Categories</li>
              </Link>
              <Link to="/create">
                <li className="listItem">Create a new Post</li>
              </Link>
              <a href="/" onClick={logout}>
                <li className="listItem">Logout</li>
              </a>
            </ul>

            <i
              onClick={handleClick}
              className={
                click ? "fas fa-times loggedIn" : "fas fa-bars loggedIn"
              }
            ></i>
            <Link to={`/dashboard/${userInfo.id}`}>
              <div type="button" className="profileImage">
                <img
                  alt="profile"
                  src={`https://mystory-node.onrender.com/${userInfo.pfp}`}
                />
                <div className="username">{userInfo.username}</div>
              </div>
            </Link>
          </>
        )}

        {!username && (
          <>
            <ul className={click ? "normal active" : "normal"}>
              <li className="listItem">
                <button
                  className="searchButton myBtn"
                  onClick={handleSearchClick}
                >
                  <i className="fa-solid fa-magnifying-glass"></i>Search
                </button>
              </li>
              {mySearch && <SearchBar onClick={handleSearchClick} />}

              <Link to="/categories">
                <li className="listItem">Categories</li>
              </Link>
              <Link to="/login" className="links">
                <li className="listItem">Login</li>
              </Link>
              <Link to="/register" className="links">
                <li className="listItem">Register</li>
              </Link>
            </ul>
            <i
              onClick={handleClick}
              className={click ? "fas fa-times" : "fas fa-bars"}
            ></i>
          </>
        )}
      </nav>
    </header>
  );
}

export default Header;
