import React from "react";
import "./css/select.css";

function CategorySelector(props) {
  return (
    <div
      id={props.id}
      type="button"
      className="select-box"
      onClick={() => {
        props.onClick(props.id, props.name);
      }}
    >
      <div id={props.id}>{props.name}</div>
    </div>
  );
}

export default CategorySelector;
